export const constants = {
  SAVED_DATA: 'Saved Media',
  DATE: 'Date',
  MEDIA_TYPE: 'Media Type',
  DOWNLOAD_LINK: 'Download Link',
  SNAPCHAT_URL: 'https://app.snapchat.com/',
  DATE_TYPES: {
    MONTH_DAY_YEAR: 'month-day-year',
    DAY_MONTH_YEAR: 'day-month-year',
    FULLY_SPELLED_OUT: 'spelt-out',
  },
};

function hasAppropriateFields(element) {
  return constants.DATE in element
    && constants.MEDIA_TYPE in element
    && constants.DOWNLOAD_LINK in element
    && element[constants.DOWNLOAD_LINK].startsWith(constants.SNAPCHAT_URL);
}

export const validateJSONResults = async (jsonData) => {
  const memoriesData = jsonData[constants.SAVED_DATA];
  if (!memoriesData) {
    return false;
  } else {
    return memoriesData.some(hasAppropriateFields);
  }
}

function formatNumber(number) {
  return number.toString().padStart(2, '0');
}

function getTime(date, use24Hr) {
  const convertedDate = new Date(date);
  const hours = convertedDate.getHours();
  const minutes = formatNumber(convertedDate.getMinutes());
  if (use24Hr) {
    return hours + ":" + minutes;
  } else {
    const amOrPm = hours >= 12 ? 'pm' : 'am';
    const convertedHours = (hours % 12) || 12;
    return convertedHours + ":" + minutes + " " + amOrPm;
  }
}

function fullySpeltOutDate(date, use24Hr) {
  const convertedDate = new Date(date);
  const MONTHS = [
    'January', 'Feburary', 'March', 'April', 'May', 'June',
    'July', 'August', 'September', 'October', 'November', 'December'
  ];
  return `${MONTHS[convertedDate.getMonth()]} `
    + `${formatNumber(convertedDate.getDate())}, `
    + `${convertedDate.getFullYear()} `
    + getTime(date, use24Hr);
}

function dateWithSlashes(date, dayBeforeMonth, use24Hr) {
  const convertedDate = new Date(date);
  let result = '';
  if (dayBeforeMonth) {
    result += `${formatNumber(convertedDate.getDate())}/`
      + `${formatNumber(convertedDate.getMonth() + 1)}`;
  } else {
    result += `${formatNumber(convertedDate.getMonth() + 1)}/`
      + `${formatNumber(convertedDate.getDate())}`;
  }
  return result
    + `/${convertedDate.getFullYear()} `
    + getTime(date, use24Hr);
}

function getAppropriateDateString(date, { datePreference, use24Hr }) {
  if (datePreference === constants.DATE_TYPES.DAY_MONTH_YEAR) {
    return dateWithSlashes(date, true, use24Hr);
  } else if (datePreference === constants.DATE_TYPES.MONTH_DAY_YEAR) {
    return dateWithSlashes(date, false, use24Hr);
  } else if (datePreference === constants.DATE_TYPES.FULLY_SPELLED_OUT) {
    return fullySpeltOutDate(date, use24Hr);
  } else {
    return date;
  }
}

function convertAndDownloadMemory(memory, datePreference) {
  return new Promise((resolve, reject) => {
    fetch(memory[constants.DOWNLOAD_LINK], {
      method: 'POST',
    })
    .then(response => response.text())
    .then(async (data) => {
      const awsLink = {
        downloadLink: data,
        dateString: getAppropriateDateString(memory[constants.DATE], datePreference),
        type: memory[constants.MEDIA_TYPE].toUpperCase() === 'VIDEO' ? 'mp4' : 'jpg',
      };
      fetch('https://snapchat-memory-saver-cors.herokuapp.com/'
      + awsLink.downloadLink)
      .then(response => response.blob())
      .then(blob => {
        const blobUrl = URL.createObjectURL(blob);
        let a = document.createElement("a");
        a.href = blobUrl;
        a.style = "display: none";
        a.setAttribute('target', '_blank');
        a.download = awsLink.dateString + '.' + awsLink.type;
        document.body.appendChild(a);
        a.click();
        resolve();
      })
      .catch(_ => resolve());
    })
    .catch(_ => resolve())
  });
}

function parallelize(dataIterable, syncOrAsyncMapFunction, concurrencyLimit) {
  const iterator = dataIterable[Symbol.iterator](),
    asyncMapFunction = async function (iteratorValue) {
      await syncOrAsyncMapFunction(iteratorValue);
    };

  if (!concurrencyLimit) {
    return Promise.all(dataIterable.map(asyncMapFunction));
  } else {
    const chain = () => {
      const next = iterator.next();
      if (!next.done) {
        return asyncMapFunction(next.value).then(chain);
      }
    };
    const parallelChains = [];
    for (let i = 0; i < concurrencyLimit; i++) {
      parallelChains.push(chain());
    }
    return Promise.all(parallelChains);
  }
}

export const handleSubmit = async (jsonData, datePreference) => {
  await parallelize(jsonData,
    (idk) => convertAndDownloadMemory(idk, datePreference), 20);
  return;
}
