import React, { useState } from 'react';
import TutorialModal from '../Modals/TutorialModal.js';
import ExplanationModal from '../Modals/ExplanationModal.js';
import './app-container.css';
import {
  validateJSONResults,
  handleSubmit,
  constants,
} from '../../Helpers/helpers.js';

const { DATE_TYPES,
  SAVED_DATA } = constants

function Upload(props) {
  const [memoriesInput, setMemoriesInput] = useState(null);
  const [memoriesInputButton, setMemoriesInputButton] = useState(null);
  const [uploadStatus, setUploadStatus] = useState(null);
  const [tutorialModal, setTutorialModal] = useState(false);

  let uploadedFileName = '';

  function onReaderLoad(event) {
    try {
      uploadStatus.classList.remove('h2');
      uploadStatus.classList.add('h6');
      const jsonData = JSON.parse(event.target.result);
      const valid = validateJSONResults(jsonData);
      if (valid) {
        memoriesInputButton.classList.remove('invalid');
        memoriesInputButton.classList.add('valid');
        props.saveFile(jsonData);
        uploadStatus.textContent = uploadedFileName +
          ' successfully uploaded. (Click to upload a different file)';
      }
    } catch (error) {
      memoriesInputButton.classList.remove('valid');
      memoriesInputButton.classList.add('invalid');
      uploadStatus.textContent = uploadedFileName +
        ' isn\'t a valid json file. Please upload a different file';
    }
  }
  function handleUpload(event) {
    event.preventDefault && event.preventDefault();
    if (event.target.files.length) {
      const file = event.target.files[0];
      uploadedFileName = file.name;
      let reader = new FileReader();
      reader.onload = onReaderLoad;
      reader.readAsText(file);
    } else {
      uploadStatus.classList.add('h2');
      uploadStatus.classList.remove('h6');
      memoriesInputButton.classList.remove('valid');
      memoriesInputButton.classList.remove('invalid');
      uploadStatus.textContent = 'No file chosen';
    }
  }

  function handleDrop(event) {
    event.preventDefault();
    if (event.dataTransfer.files && event.dataTransfer.files.length > 0) {
      handleUpload({ target: { files: event.dataTransfer.files } });
    }
  }

  return (
    <React.Fragment>
      <h3>
        1. Upload Snapchat memory file
        </h3>
      <div className="col-sm">
        <input type="file" id="memories-input" hidden="hidden"
          onChange={handleUpload} ref={(input) => setMemoriesInput(input)} />
        <div type="button" id="memories-input-btn"
          onDrop={handleDrop}
          onDragOver={e => e.preventDefault()}
          onClick={e => memoriesInput && memoriesInput.click()}
          ref={(button) => setMemoriesInputButton(button)}>
          <span className="h2" id="upload-status"
            ref={(span) => setUploadStatus(span)}>
            No file chosen
          </span>
        </div>
        <p>
        </p>
      </div>
      <div className="col-sm">
        <button type="button" className="btn btn-primary"
          onClick={() => setTutorialModal(!tutorialModal)}>
          <p className="question-icon" id="explanation-toggle" href="#">&#x3f;</p>
            How do I get my snapchat memory file?
          </button>
      </div>
      <TutorialModal {...{
        isOpen: tutorialModal,
        toggle: () => setTutorialModal(!tutorialModal)
      }} />
    </React.Fragment>
  );
}

function DateSelect(props) {
  const [use24Hr, toggle24Hr] = useState();
  const [datePreference, setDatePreference]
    = useState(DATE_TYPES.FULLY_SPELLED_OUT);
  const dateOptions = [
    {
      label: 'Fully Spelled Out',
      value: DATE_TYPES.FULLY_SPELLED_OUT,
      example: <React.Fragment>
        e.g. May 5, 2020 <span className="time-example">
          {use24Hr ? '15:00' : '3:00 pm'}
        </span>
      </React.Fragment>
    },
    {
      label: 'Month day year (mm/dd/yyyy)',
      value: DATE_TYPES.MONTH_DAY_YEAR,
      example: <React.Fragment>
        e.g. 05/09/2020 <span className="time-example">
          {use24Hr ? '15:00' : '3:00 pm'}
        </span>
      </React.Fragment>
    },
    {
      label: 'Day month year (dd/mm/yyyy)',
      value: DATE_TYPES.DAY_MONTH_YEAR,
      example: <React.Fragment>
        e.g. 09/05/2020 <span className="time-example">
          {use24Hr ? '15:00' : '3:00 pm'}
        </span>
      </React.Fragment>
    },
  ];

  function getClassName(value) {
    return "date-option ".concat(datePreference === value ? 'meme' : '');
  }
  return (
    <React.Fragment>
      <h3>
        2. Select a date format
      </h3>
      <div id="date-format" className="row">
        {dateOptions.map(({ label, value, example }, index) => (
          <div key={index} className="col-sm">
            <div className={getClassName(value)}
              id={value} onClick={_ => {
                setDatePreference(value);
                props.setDate({ datePreference: value, use24Hr })
              }}>
              <input className="row-list-input" type="radio"
                name="date-input" />
              <label className="date-example row-list-label"
                htmlFor={value}>
                {label}
              </label>
              <p className="date-example">
                {example}
              </p>
            </div>
          </div>
        ))}
        <div className="col-sm">
          <input type="checkbox" id="time-input"
            onClick={e => {
              toggle24Hr(e.target.checked)
              props.setDate({ datePreference, use24Hr: e.target.checked })
            }} />
          <label className="row-list-label" htmlFor="time-input">
            Use 24 hour time
          </label>
        </div>
      </div>
    </React.Fragment>
  );
}

function Submit(props) {
  const [explanationModal, setExplanationModal] = useState(false);
  const [buttonDisabled, setButtonDisabled] = useState(false);

  return (
    <React.Fragment>
      <h3>3. Download memories</h3>
      <div id="submit-download" className="col-sm">
        <button id="submit-btn" className={buttonDisabled ? 'disabled' : ''}
          onClick={async () => {
            setButtonDisabled(true);
            await props.handleSubmit();
            setButtonDisabled(false);
          }}>
          Start download
            <div className="spinner-border text-dark download-spinner"
            role="status">
            <span className="visually-hidden">Loading...</span>
          </div>
        </button>
      </div>
      <div className="col-sm">
        <button type="button" className="btn btn-primary"
          onClick={() => setExplanationModal(!explanationModal)}>
          <p className="question-icon" id="explanation-toggle" href="#">
            &#x3f;
          </p>
            How are snapchat memories downloaded?
          </button>
      </div>
      <ExplanationModal {...{
        isOpen: explanationModal,
        toggle: () => setExplanationModal(!explanationModal)
      }} />
    </React.Fragment>
  );
}

export default function AppContainer() {
  const [jsonData, setJsonData] = useState();
  const [datePreference, setDatePreference] = useState({
    datePreference: DATE_TYPES.FULLY_SPELLED_OUT,
    use24Hr: false,
  });

  function saveFile(fileData) {
    setJsonData(fileData);
  }

  const components = [
    <Upload {...{ saveFile }} />,
    <DateSelect {...{ setDate: (date) => setDatePreference(date) }} />,
    <Submit {...{
      handleSubmit:
        async () => await handleSubmit(jsonData[SAVED_DATA], datePreference)
    }}
    />,
  ];
  return (
    <React.Fragment>
      <div className="app-container mx-auto">
        {components.map((Component, index) => (
          <div key={index}
          className={
            "row container ".concat(!!index ? (!!jsonData ? '' : 'disabled') : '')
          }>
            {Component}
          </div>
        ))}
      </div>
    </React.Fragment>
  )
}
